<template>
  <div>
    <main>
      <HeroSection :hero="data.hero" />

      <div class="course-section-wrapper">
        <section id="courses" class="courses-section container">
          <h2 class="section-title">KHÓA HỌC NỔI BẬT</h2>
          <div class="course-list">
            <CourseCard v-for="(course, i) in data.courses" :key="i" :course="course" />
          </div>
        </section>
      </div>

      <AboutSection :about="data.about" />

      <TeachersSection :teachers="data.teachers" />

    </main>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'

// Import component
import HeroSection from '../components/HeroSection.vue'
import CourseCard from '../components/CourseCard.vue'
import TeachersSection from '../components/TeachersSection.vue'
import AboutSection from '../components/AboutSection.vue'

const data = ref({
  hero: { cta: {} },
  courses: [],
  about: { highlights: [] },
  teachers: [],
})

onMounted(async () => {
  try {
    const response = await fetch(`${process.env.VUE_APP_BASE_URL}data.json`)
    if (!response.ok) throw new Error('Network response was not ok')
    data.value = await response.json()
  } catch (error) {
    console.error('Không thể tải file data.json:', error)
  }
})

</script>

<style scoped>
.course-section-wrapper {
  background-color: #e6f7ff;
  padding: 40px 0;
  width: 100%;
}

.section-title {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 40px;
    color: #333;
}

.course-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
}
</style>
