<template>
  <div class="gallery-page">
    <div class="page-header">
      <h1>{{ gallery.title }}</h1>
    </div>
    
    <div class="gallery-container">
      <div v-for="(image, index) in gallery.images" :key="index" class="gallery-item">
        <img :src="`${baseUrl}${image}`" :alt="`Gallery image ${index + 1}`" class="gallery-image" loading="lazy">
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

const gallery = ref({
  title: '',
  images: []
});
const baseUrl = ref(process.env.VUE_APP_BASE_URL || '/');

onMounted(async () => {
  try {
    const response = await fetch(`${baseUrl.value}data.json`)
    if (!response.ok) throw new Error('Network response was not ok')
    const data = await response.json()
    gallery.value = data.gallery || { title: '', images: [] }
  } catch (error) {
    console.error('Không thể tải file data.json:', error)
  }
})
</script>

<style scoped>
.gallery-page {
  padding: 80px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin-bottom: 40px;
}

.page-header h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.gallery-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.gallery-item {
  border: 1px solid #eee;
  border-radius: 8px;
  overflow: hidden;
}

.gallery-image {
  width: 100%;
  height: auto;
  display: block;
}
</style>