<template>
  <div class="about-us-page">
    <div class="page-header">
      <h1>{{ aboutUsPage.introduction.title }}</h1>
    </div>
    
    <div class="about-us-container">
      <div v-for="(paragraph, index) in aboutUsPage.introduction.content" :key="index" class="about-us-paragraph">
        <p>{{ paragraph }}</p>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

const aboutUsPage = ref({
  introduction: {
    title: '',
    content: []
  }
});

onMounted(async () => {
  try {
    const response = await fetch(`${process.env.VUE_APP_BASE_URL}data.json`)
    if (!response.ok) throw new Error('Network response was not ok')
    const data = await response.json()
    aboutUsPage.value = data.aboutUsPage || { introduction: { title: '', content: [] } }
  } catch (error) {
    console.error('Không thể tải file data.json:', error)
  }
})
</script>

<style scoped>
.about-us-page {
  padding: 80px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin-bottom: 40px;
}

.page-header h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.about-us-container {
  line-height: 1.6;
}

.about-us-paragraph {
  margin-bottom: 20px;
}
</style>